
:root{
    --gap:7px;
    --width:70px;
    --height:70px;
    --isize:120px;
    --hsize:60px;
    --top:0%;
}

.score_won{
    position:absolute;
    top:var(--top);
    left:25%;
    transform: translateX(-50%);
    font-size: var(--hsize);
}

.score_draw{
    position:absolute;
    top:var(--top);
    left:50%;
    transform: translateX(-50%);
    font-size: var(--hsize);
}

.score_lost{
    position:absolute;
    top:var(--top);
    left:75%;
    transform: translateX(-50%);
    font-size: var(--hsize);
}

.x_won{
    width:100vw;
    height:100vh;
    background-color: red;
    
    display:flex;
    justify-content:center;
    align-items: center;
}

.o_won{
    width:100vw;
    height:100vh;
    background-color: blue;

    display:flex;
    justify-content:center;
    align-items: center;
}

.draw{
    width:100vw;
    height:100vh;
    background-color: black;

    display:flex;
    justify-content:center;
    align-items: center;
}

.draw_h{
    color:white;
}

.x_won_h{
    color:white;
}

.o_won_h{
    color:white;
}

.screen{
    width:320px;
    height:100vh;
    background-color: white;
    margin-inline:auto;

    display:flex;
    justify-content:center;
    align-items: center;
}

.board{
    background-color: black;

    display:grid;
    gap:var(--gap);

    grid-template-columns:auto auto auto;
    grid-template-rows:auto auto auto;
}

.box{
    height: var(--height);
    width: var(--width);
    background-color: white;

    display:flex;
    justify-content: center;
    align-items: center;

    position: relative; 
    transition: background-color 0.3s ease, box-shadow 0.3s ease;
}

.floating_box_r{
    height: var(--height);
    width: var(--width);

    display:flex;
    justify-content: center;
    align-items: center;

    position: relative; 
    transition: background-color 0.3s ease, box-shadow 0.3s ease;

    background-color: red; 
    box-shadow: 0 0 20px red(255, 0, 0, 0.5); 
    animation: pulse-box 1.5s infinite; 
}

.floating_box_b{
    height: var(--height);
    width: var(--width);

    display:flex;
    justify-content: center;
    align-items: center;

    position: relative; 
    transition: background-color 0.3s ease, box-shadow 0.3s ease;

    background-color: blue; 
    box-shadow: 0 0 20px blue(0, 0, 255, 0.5); 
    animation: pulse-box 1.5s infinite; 
}


@keyframes pulse-box {
    0% {
        transform: scale(1);
        box-shadow: 0 0 15px rgba(46, 204, 113, 0.4);
    }
    50% {
        transform: scale(1.05);
        box-shadow: 0 0 30px rgba(46, 204, 113, 0.6);
    }
    100% {
        transform: scale(1);
        box-shadow: 0 0 15px rgba(46, 204, 113, 0.4);
    }
}

.x_icon{
    font-size:var(--isize);
    color:red;
}

.o_icon{
    font-size:var(--isize);
    color:blue;
}

.x_icon_f{
    font-size:var(--isize);
    color:white;
}

.o_icon_f{
    font-size:var(--isize);
    color:white;
}

@media(min-width:320px) and (max-width:479px) and (orientation:portrait){
    /* 148x320 (19.5:9 aspect ratio) */
    :root{
        --gap:7px;
        --width:70px;
        --height:70px;
        --isize:50px;
        --hsize:17px;
        --top:25%;
    }
}

@media(min-width:480px) and (max-width:767px) and (orientation:portrait){
    /* 222x480 (19.5:9 aspect ratio) */
    :root{
        --gap:1px;
        --width:100px;
        --height:100px;
        --isize:70px;
        --hsize:23px;
        --top:25%;
    }
}

@media(min-width:320px) and (max-width:479px) and (orientation:landscape){
    /* 320x148 (19.5:9 aspect ratio) */
    :root{
        --gap:3px;
        --width:30px;
        --height:30px;
        --isize:20px;
        --h_size:7px;
    }
}

@media(min-width:480px) and (max-width:767px) and (orientation:landscape){
    /* 480x222 (19.5:9 aspect ratio) */
    :root{
        --gap:5px;
        --width:50px;
        --height:50px;
        --isize:35px;
        --h_size:12px;
    }
}

@media(min-width:768px) and (max-width:1023px){
    /* 768x576 (4:3 aspect ratio) */
    :root{
        --gap:13px;
        --width:130px;
        --height:130px;
        --isize:90px;
        --hsize:40px;
    }
}

@media(min-width:1024px) and (max-width:1279px){
    /* 1024x768 (4:3 aspect ratio) */
    :root{
        --gap:17px;
        --width:170px;
        --height:170px;
        --isize:120px;
        --hsize:60px;
    }
}

@media(min-width:1280px) and (max-width:1439px){
    /* 1280x720 (16:9 aspect ratio) */
    :root{
        --gap:16px;
        --width:160px;
        --height:160px;
        --isize:110px;
        --hsize:50px;
    }
}

@media(min-width:1440px) and (max-width:1919px){
    /* 1440x810 (16:9 aspect ratio) */
    :root{
        --gap:18px;
        --width:180px;
        --height:180px;
        --isize:125px;
        --hsize:60px;
    }
}

@media(min-width:1920px){
    /* 1920x1080 (16:9 aspect ratio) */
    :root{
        --gap:25px;
        --width:250px;
        --height:250px;
        --isize:175px;
        --hsize:80px;
    }
}
